<template>
  <div>
    <navbar></navbar>
    <div class="container">
      <dataForm :option="option" ref="dataForm"></dataForm>
      <div class="button-fluid">
        <van-button block type="info" @click="change">
          
        </van-button>
        <van-button block type="info" @click="changeColumns">
          
        </van-button>
        <van-button block type="info" @click="restore">
          
        </van-button>
        <van-button block type="info" @click="clear">
          
        </van-button>
      </div>
    </div>
  </div>
</template>
<script>
import demoApi from "@/api/demo";
import { Button } from "vant";
import dataForm from "@/components/base/form/dataForm";
export default {
  name: "DataForm",
  components: {
    [Button.name]: Button,
    dataForm
  },
  data() {
    let codeStr = "";
    return {
      codeStr,
      option: {
        content: [
          {
            label: "",
            name: "name",
            required: true,
            validator: val => {
              return val.length == 1;
            },
            value: "11111111111"
          },
          {
            label: "",
            name: "code",
            required: true,
            validator: val => {
              return val.length == 6;
            },
            scopedSlots: {
              button: () => {
                const codeStr = this.codeStr;
                const option = {
                  props: {
                    size: "small",
                    type: "primary",
                    disabled: codeStr != ""
                  },
                  on: {
                    click: this.sendCode
                  },
                  style: { width: "100px" }
                };
                return <van-button {...option}>{this.codeStr}</van-button>;
              }
            },
            value: "11111111111"
          },
          {
            label: "",
            name: "price",
            type: "price",
            value: "11111111111"
          },
          {
            label: "",
            name: "formatter",
            value: "",
            props: {
              placeholder: ",",
              formatter: value => value.replace(/\d/g, "")
            }
          },
          {
            label: "",
            name: "password",
            type: "password",
            value: "123456"
          },
          {
            label: "",
            name: "remark",
            type: "textarea",
            value: "123456"
          },
          {
            label: "",
            name: "switch",
            type: "switch",
            value: true
          },
          {
            label: "",
            name: "date",
            type: "date",
            value: "2020-05-20"
          },
          {
            label: "",
            name: "datetime",
            type: "date",
            value: "2020-05-20 14:44:00",
            props: {
              type: "datetime"
            }
          },
          {
            label: "",
            name: "yearMonth",
            type: "date",
            value: "2020-11",
            props: {
              type: "year-month"
            }
          },
          {
            label: "",
            name: "time",
            type: "date",
            value: "18:50",
            props: {
              type: "time"
            }
          },
          {
            label: "",
            name: "select",
            type: "select",
            value: "0",
            props: {
              columns: [
                { id: 1, name: "" },
                { id: 2, name: "" },
                { id: 3, name: "" }
              ]
            }
          },
          {
            label: "",
            name: "multiple",
            type: "multiple",
            value: [1],
            props: {
              columns: [
                { id: 1, name: "" },
                { id: 2, name: "" },
                { id: 3, name: "" }
              ]
            }
          },
          {
            label: "",
            name: { id: "list", name: "listName" },
            type: "list",
            value: { id: 1, name: "" },
            props: {
              api: demoApi.searchList
            },
            on: {
              searchBeforeCallback: res => res,
              searchAfterCallback: res => res,
              submitBeforeCallback: res => res,
              submit: res => res
            }
          }
        ],
        submit: data => {
          console.log(data);
        }
      }
    };
  },
  created() {},
  computed: {},
  methods: {
    change() {
      this.$refs.dataForm.setValue({
        name: "",
        password: "",
        switch: false,
        date: "2000-08-05",
        datetime: "2000-08-05 16:24:30",
        yearMonth: "2011-11",
        time: "06:50",
        select: 3,
        list: { id: 50, name: "" }
      });
    },
    changeColumns() {
      this.$refs.dataForm.setData({
        select: [
          { id: 11, name: "" },
          { id: 22, name: "" },
          { id: 33, name: "" },
          { id: 34, name: "" }
        ],
        multiple: [
          { id: 11, name: "" },
          { id: 22, name: "" },
          { id: 33, name: "" },
          { id: 34, name: "" }
        ]
      });
    },
    restore() {
      this.$refs.dataForm.restore();
    },
    clear() {
      this.$refs.dataForm.clear();
    },
    sendCode() {
      let time = 59;
      this.codeStr = time + "";
      let t = setInterval(() => {
        if (time == 1) {
          clearInterval(t);
          this.codeStr = "";
        }
        time--;
        this.codeStr = time + "";
      }, 1000);
    }
  }
};
</script>
<style lang="less" scoped></style>
